import "../globals";
import "p5/lib/addons/p5.sound";
import autoBind from "auto-bind";

const sketch = (p5) => {
  let isRunning = false;
  let textColor;
  let ploter;

  p5.setup = () => {
    let cnv = p5.createCanvas(600, 600);
    p5.colorMode(p5.HSB);
    p5.textAlign(p5.CENTER);
    textColor = p5.color(80, 360, 30);
    ploter = new WavePlotter();
    ploter.setColor(textColor);
    cnv.mousePressed(() => {
      console.log(ploter);
      if (isRunning) {
        isRunning = !isRunning;
        ploter.stopSound();
      } else {
        isRunning = !isRunning;
        ploter.startSound();
      }
    });
  };

  p5.draw = () => {
    if (isRunning) {
      ploter.draw();
    } else {
      p5.push();
      p5.background(0);
      p5.noStroke();
      p5.fill(255);
      p5.textSize(24);
      p5.text("Presiona el canvas", p5.width / 2, p5.height / 2);
      p5.pop();
    }
  };

  p5.keyPressed = () => {
    switch (p5.key.toLowerCase()) {
      case "1":
        ploter.setSound("sine");
        break;
      case "2":
        ploter.setSound("triangle");
        break;
      case "3":
        ploter.setSound("square");
        break;
      case "4":
        ploter.setSound("sawtooth");
        break;
      case "z":
        ploter.setFreqSound(297.775);
        break;
      case "x":
        ploter.setFreqSound(311.16);
        break;
      case "c":
        ploter.setFreqSound(324.53);
        break;
      case "v":
        ploter.setFreqSound(331.22);
        break;
      case "b":
        ploter.setFreqSound(344.62);
        break;
      case "n":
        ploter.setFreqSound(358);
        break;
      case "m":
        ploter.setFreqSound(371.35);
        break;
      case ",":
        ploter.setFreqSound(378.05);
        break;
      case "l":
        ploter.changeLog();
        break;
      // case "a":
      //   isRunning = true;
      //   ploter.startSound();
      //   break;
      // case "s":
      //   isRunning = false;
      //   ploter.stopSound();
      //   break;
      default:
        console.log("Presiona otra tecla...");
    }
  };

  class WavePlotter {
    constructor() {
      this.textColor = p5.color(20, 300, 300);
      this.bNormalize = true;
      this.centerClip = false;
      this.isRunning = false;
      this.isLog = true;
      this.actualFreq;
      this.osc1 = new p5.constructor.Oscillator(440, "sine");
      // this.textInfo = this.textInfo.bind(this);
      this.startSound = this.startSound.bind(this);
      // this.stopSound = this.stopSound.bind(this);
      // this.setSound = this.setSound.bind(this);
      // this.setFreqSound = this.setFreqSound.bind(this);
      // this.changeLog = this.changeLog.bind(this);
      // this.setColor = this.setColor.bind(this);
      // this.draw = this.draw.bind(this);
      // this.autoCorrelate = this.autoCorrelate.bind(this);
      // this.logConvertion = this.logConvertion.bind(this);
      this.frecuencias = new p5.constructor.FFT();
      this.frecuencias.setInput(this.osc1);
      this.spectrum = this.frecuencias.analyze.bind(this);
      autoBind(this);
    }

    textInfo(f, vol) {
      // Texto de informacion
      p5.push();
      p5.noStroke();
      p5.fill(this.textColor);
      p5.textSize(16);
      p5.text("Osc: " + this.osc1.getType(), (p5.width * 1) / 9, 50);
      p5.text(p5.int(f) + " hz", p5.width / 2, 50);
      p5.text(vol * 100 + " vol", (p5.width * 7) / 9, 50);
      p5.pop();
    }
    startSound() {
      this.osc1.start();
    }
    stopSound() {
      this.osc1.stop();
    }
    setSound(type) {
      this.osc1.setType(type);
    }
    setFreqSound(f) {
      this.osc1.freq(f);
    }
    changeLog() {
      this.isLog = !this.isLog;
    }
    setColor(color) {
      this.textColor = color;
    }
    draw = () => {
      // Sliders
      p5.background(0);
      this.actualFreq = this.logConvertion(p5.mouseX, 0, p5.width, 20, 20000);
      let vol = p5.map(p5.mouseY, p5.height, 0, 0, 0.75);
      this.osc1.freq(this.actualFreq);
      this.osc1.amp(vol);
      this.textInfo(this.actualFreq, vol);
      //
      // Espectro de frecuencias
      let spectrum = this.spectrum();
      console.log(spectrum);
      for (let i = 0; i < spectrum.length; i++) {
        let c = p5.map(i, 0, spectrum.length, 0, 255);
        p5.fill(c, 255, 255); //remove stroke(255);
        // En caso de ser LOG
        if (this.isLog) {
          let a = p5.map(
            Math.log(i),
            0,
            Math.log(spectrum.length),
            0,
            p5.width
          );
          let b = p5.map(spectrum[i], 0, 255, 0, p5.height / 3);
          p5.rect(a, p5.height, p5.width / spectrum.length, -b);
          p5.fill(textColor);
          p5.text("LOG", (p5.width * 7) / 9, 50);
          // En caso de ser LIN
        } else {
          let a = p5.map(i, 0, spectrum.length, 0, p5.width);
          let b = p5.map(spectrum[i], 0, 255, 0, p5.height / 3);
          p5.rect(a, p5.height, p5.width / spectrum.length, -b);
          p5.fill(textColor);
          p5.text("LIN", (p5.width * 7) / 9, 50);
        }
      }
      // Forma de onda en el tiempo
      // let waveform = this.frecuencias.waveform();
      // p5.noFill();
      // p5.beginShape();
      // p5.stroke(100, 255, 255);
      // for (let i = 0; i < waveform.length; i++) {
      //   let x = p5.map(i, 0, waveform.length, 0, p5.width);
      //   let y = p5.map(waveform[i], -1, 1, p5.height / 3, (p5.height * 2) / 3);
      //   p5.vertex(x, y);
      // }
      // p5.endShape();
      // let corrBuff = this.autoCorrelate(waveform);
      // p5.beginShape();
      // p5.stroke(180, 255, 255);
      // for (let i = 0; i < corrBuff.length; i++) {
      //   let w = p5.map(i, 0, corrBuff.length, 0, p5.width);
      //   let h = p5.map(
      //     corrBuff[i],
      //     -1,
      //     1,
      //     (p5.height * 4) / 9,
      //     (p5.height * 2) / 9
      //   );
      //   p5.curveVertex(w, h);
      // }
      // p5.endShape();
    };
    logConvertion(position, min, max, minLog, maxLog) {
      var minp = min;
      var maxp = max;
      var minv = Math.log(minLog);
      var maxv = Math.log(maxLog);
      var scale = (maxv - minv) / (maxp - minp);
      return Math.exp(minv + scale * (position - minp));
    }
    autoCorrelate(buffer) {
      var newBuffer = [];
      var nSamples = buffer.length;
      // var autocorrelation = [];
      // center clip removes any samples under 0.1
      if (this.centerClip) {
        var cutoff = 0.1;
        for (var i = 0; i < buffer.length; i++) {
          var val = buffer[i];
          buffer[i] = Math.abs(val) > cutoff ? val : 0;
        }
      }
      for (let lag = 0; lag < nSamples; lag++) {
        let sum = 0;
        for (let index = 0; index < nSamples; index++) {
          let indexLagged = index + lag;
          if (indexLagged < nSamples) {
            let sound1 = buffer[index];
            let sound2 = buffer[indexLagged];
            let product = sound1 * sound2;
            sum += product;
          }
        }
        // average to a value between -1 and 1
        newBuffer[lag] = sum / nSamples;
      }
      if (this.bNormalize) {
        let biggestVal = 0;
        for (let index = 0; index < nSamples; index++) {
          if (p5.abs(newBuffer[index]) > biggestVal) {
            biggestVal = p5.abs(newBuffer[index]);
          }
        }
        for (let index = 0; index < nSamples; index++) {
          newBuffer[index] /= biggestVal;
        }
      }
      return newBuffer;
    }
  }
};
export default sketch;
