/*eslint-disable*/
export default function sketch(p5) {
    var cnv;
    let graficas = [];
    let numGraficas = 6;
    let factor = 0.8
    let amplitude = 30;
    let sliderGraficas, sliderFactor, sliderAmplitude;

    p5.setup = () => {
        p5.createCanvas(800,600);
        graficas = [numGraficas];
        let polarInit = p5.createVector(amplitude*4,amplitude*2);
        let timeInit =  p5.createVector(amplitude*10,amplitude*2);
        let centroPolar, initGrafica, y, yT;
        let amplitudeGraf = amplitude;
        
        for(let i = 0; i < numGraficas; i++) {
            // Calacula el centro del plotter polar y la grafica correspondiente en el tiempo
            let armonicos = i + 1;
            let y = polarInit.y + (i * amplitude * 2.2);
            let yT = timeInit.y + (i * amplitude * 2.2);
            centroPolar = p5.createVector(polarInit.x,y);
            initGrafica = p5.createVector(timeInit.x,yT);
            // almacena una nueva grafica que grafica el armonico final
            graficas[i] = new PloterFinalFourier(centroPolar, initGrafica, amplitudeGraf, armonicos);
            // se escala el valor de la amplitud para cada nuevo armonico
            amplitudeGraf = amplitudeGraf * factor
        }
        y = polarInit.y + (numGraficas * amplitude * 2.4);
        yT = timeInit.y + (numGraficas * amplitude * 2.4);
        centroPolar = p5.createVector(polarInit.x,y);
        initGrafica = p5.createVector(timeInit.x,yT);
        graficas[numGraficas] = new PloterFourier(centroPolar, initGrafica, amplitude, numGraficas);
        graficas[numGraficas].changeFunction(5);
        graficas[numGraficas].changeFactor(factor);
        // frameRate(10);   
    }

    p5.draw = () => {
        p5.background(0);
        for(let i = 0; i < graficas.length; i++) {
            // graficas[i].setArmonicos(sliderGraficas.value()); 
            // graficas[i].changeFactor(sliderFactor.value()); 
            // graficas[i].setAmp(sliderAmplitude.value()); 
            graficas[i].actualizar();              
        }             
    }

    class PloterFourier {
        constructor(centroPolarPlot, initTimePlot, amplitude, armonicos) {
            this.tiempo = 0;
            this.timeStep = 0.02;
            // this.velTime = -0.05;
            this.amp = amplitude;
            // this.piFactor = (3 / (1 * PI));
            // this.radio = this.amp + this.piFactor;
            this.radio = this.amp;
            this.resolucion = armonicos;
    
            this.arrayWaveTime = [];
            this.arrayEspyro = [];
    
            this.centroPolar = centroPolarPlot;
            this.puntoExterior;
            this.initPlotTime = initTimePlot;
    
            this.sizeTiempoPlot = 500;
            this.sizePolarPlot = 1000;
            this.factor = 0;
            this.funcActual = 1;
    
            this.colorAxis = p5.color(2, 6, 89);
            this.colorLinea = p5.color(242, 203, 5,100);
            this.colorExt = p5.color( 217, 142, 4);
        }
    
        actualizar() {
            // background(0);   
            this.dibujaAxis();
            this.puntoExterior = this.centroPolar.copy();
            let amplitud = this.amp;
            for(let i = 0; i < this.resolucion; i ++) {
                this.centroActual = this.puntoExterior.copy();
                let n;
                
                switch(this.funcActual) {
                    case 1:
                        // TRIANGULAR
                        n = i * 2 + 1; // numeros impares   
                        this.radio = this.amp * (6.5 / (n*n*p5.PI*p5.PI) * p5.pow((-1),((n-1)/2)));
                        this.puntoExterior.x += (this.radio * p5.cos(n * this.tiempo));
                        this.puntoExterior.y += (this.radio * p5.sin(n * this.tiempo));
                        break;
                        
                    case 2:
                        //SQUARE;
                        n = i * 2 + 1; // numeros impares   
                        this.radio = this.amp * (2.5 / (n * p5.PI));
                        this.puntoExterior.x += (this.radio * p5.cos(n * this.tiempo));
                        this.puntoExterior.y += (this.radio * p5.sin(n * this.tiempo));
                        break;
                    case 3:
                        // SAWTOOTH
                        n = i + 1;
                        this.radio = 2.5 * this.amp * ((1 / 2) - (1 /p5.PI)) * (1/n);
                        // Calcula el punto exterior;
                        this.puntoExterior.x += (this.radio * p5.cos(n * this.tiempo));
                        this.puntoExterior.y += (this.radio * p5.sin(n * this.tiempo));
                        break;
                    case 4:
                            // SINE
                            this.radio =  this.amp;
                            // Calcula el punto exterior;
                            this.puntoExterior.x += (this.radio * p5.cos(this.tiempo));
                            this.puntoExterior.y += (this.radio * p5.sin(this.tiempo));
                            break;
                    case 5:
                            // SINE
                            n = i + 1; 
                            this.radio =  amplitud;
                            // if(i === 0)
                            
                            // else 
                            //     this.radio =  
                            // this.amp *= (this.factor / n); 
                            // print(this.radio);
                            
                            // Calcula el punto exterior;
                            this.puntoExterior.x += (this.radio * p5.cos(this.tiempo*n));
                            this.puntoExterior.y += (this.radio * p5.sin(this.tiempo*n));
                            amplitud *= this.factor;
                            break;
                            
                }
                this.dibujaPolarStructure();
            }
            this.guardaPolar();
            this.guardaTiempo();
            this.dibujaBridgeStroke();
            this.dibujaTimeShape();
            this.dibujaPolarShape();
            // print('');
            // Actualiza el tiempo en el canvas
            // tiempo += velTime;
            this.tiempo += this.timeStep;
    
        }
    
        guardaTiempo() {
            // Guarda el valor de 'y' del punto exterior
            // del circulo mas pequeo a traves  del tiempo
            this.arrayWaveTime.unshift(this.puntoExterior.y-this.centroPolar.y);
            // Limita el numero de valores del  de tiempo
            if(this.arrayWaveTime.length > this.sizeTiempoPlot) {
                this.arrayWaveTime.pop();
            }
        }
    
        guardaPolar() {
            // Guarda la posicion en 'y' del Punto Exterior
            // del circulo mas pequeo del arreglo
            this.arrayEspyro.unshift(this.puntoExterior.x, this.puntoExterior.y);
            // Limita el numero de muestras en el arreglo
            // de la grafica polar
            if(this.arrayEspyro.length > this.sizePolarPlot) {
                this.arrayEspyro.pop();
            }
        }
    
        dibujaAxis() {
            p5.push();
            // stroke(255,100);
            p5.stroke(this.colorAxis)
            p5.strokeWeight(1);
            // Dibuja los ejes en el plano del tiempo
            p5.line(this.initPlotTime.x - 50, this.initPlotTime.y, this.initPlotTime.x + 450, this.initPlotTime.y);
            p5.line(this.initPlotTime.x, this.initPlotTime.y - this.amp * 2, this.initPlotTime.x, this.initPlotTime.y + this.amp * 2);
            // Dibuja los ejes del plano polar
            p5.line(this.centroPolar.x - (2 * this.amp), this.centroPolar.y,this.centroPolar.x + (2 * this.amp), this.centroPolar.y)
            p5.line(this.centroPolar.x, this.centroPolar.y - (2 * this.amp), this.centroPolar.x, this.centroPolar.y + (2 * this.amp));
            p5.pop();
        }
    
    
        dibujaPolarStructure() {
            // Dibuja estructura del espirografo
            p5.push();
            // Dibuja circulo
            p5.strokeWeight(1);
            p5.stroke(255,100);
            p5.noFill();
            p5.ellipse(this.centroActual.x, this.centroActual.y, this.radio * 2);
            // Dibuja el radio
            p5.line(this.centroActual.x, this.centroActual.y,this.puntoExterior.x,this.puntoExterior.y);
            // Dibuja los puntos del centro y del exterior
            p5.strokeWeight(3);
            p5.point(this.centroActual.x, this.centroActual.y);
            p5.point(this.puntoExterior.x,this.puntoExterior.y);
            p5.pop();
        }
    
    
        dibujaBridgeStroke() {
            // Dibuja una linea desde 'y' actual en grafica polar
            // hasta posicion actual en 'y' grafica del tiempo
            p5.push();
            p5.stroke(127);
            p5.line(this.puntoExterior.x,this.puntoExterior.y,this.initPlotTime.x, this.arrayWaveTime[0]+this.initPlotTime.y);
            p5.pop();
        }
    
        dibujaPolarShape() {
            // Dibuja la grafica de la posicion en 'y' del
            // punto exterior del circulo mas pequeo
            p5.push();
            p5.strokeWeight(3);
            // stroke(210,120,50,100);
            p5.stroke(this.colorExt);
            p5.beginShape();
            p5.noFill();
            for(let i = 0; i < this.arrayEspyro.length/2; i+=2) {
                // point(this.arrayEspyro[i],this.arrayEspyro[i+1]);
                p5.vertex(this.arrayEspyro[i],this.arrayEspyro[i+1]);
            }
            p5.endShape();
            p5.pop();
        }
    
        dibujaTimeShape() {
            // Dibuja la grafica de onda con respecto al tiempo
            p5.push();
            p5.strokeWeight(2);
            // p5.stroke(200);
            p5.stroke(this.colorLinea)
            p5.beginShape();
            p5.noFill();
            for(let i = 0; i < this.arrayWaveTime.length; i++) {
                p5.vertex(i+this.initPlotTime.x,this.arrayWaveTime[i]+this.initPlotTime.y);
            }
            p5.endShape();
            p5.pop();
        }
    
        getArmonicos() {
            return this.resolucion;
        }
    
        setArmonicos(armonicos) {
            this.resolucion = armonicos;
        }
    
        getAmp() {
            return this.amp;
        }
    
        setAmp(amp) {
            this.amp = amp;
        }
        setTimeStep(time) {
            this.timeStep = time;
        }
    
        changeFunction(numOfFunc) {
            this.funcActual = numOfFunc;
        }
    
        changeFactor(f) {
            this.factor = f;
        }
    }
    
    
    class PloterFinalFourier extends PloterFourier {
        
        constructor(centroPolarPlot, initTimePlot, amplitude, armonicos) {
            super(centroPolarPlot, initTimePlot, amplitude, armonicos);
            // this.changeFunction(5);
            this.timeStep = 0.02;
        }
        
        actualizar() {
            this.dibujaAxis();
            this.puntoExterior = this.centroPolar.copy();
            this.centroActual = this.puntoExterior.copy();
            this.radio =  this.amp;
            // print(this.resolucion);
            this.puntoExterior.x += (this.radio * p5.cos(this.resolucion * this.tiempo));
            this.puntoExterior.y += (this.radio * p5.sin(this.resolucion * this.tiempo));
            
            this.dibujaPolarStructure();
            
            this.guardaPolar();
            this.guardaTiempo();
            this.dibujaBridgeStroke();
            this.dibujaTimeShape();
            this.dibujaPolarShape();
            // }
            // Actualiza el tiempo en el canvas
            // tiempo += velTime;
            this.tiempo += this.timeStep;
            // print(this.radio);
            // print('');
        }
        
    }
}