import "../globals";
import "p5/lib/addons/p5.sound";
// console.log(this);
const sketch = (p5) => {
  let bol;
  let playing = false;
  p5.setup = () => {
    p5.createCanvas(500, 500);
    p5.background(0);
    p5.smooth();
    bol = new Bolita(100, 100, 30);
  };
  p5.draw = () => {
    p5.noStroke();
    p5.fill(255);
    p5.ellipse(p5.width / 2, p5.height / 2, 50);
    bol.draw();
  };
  p5.keyPressed = () => {
    if (p5.key == "a") bol.playOsc();
    if (p5.key == "s") bol.stopOsc();
  };
  class Bolita {
    constructor(x, y, s) {
      this.pos = p5.createVector(x, y);
      this.size = s;
      this.vel = p5.createVector(p5.random(p5.width), p5.random(p5.height));
      this.vel.normalize();
      // this.osc = 10;
      this.osc = new p5.constructor.Oscillator();
      this.playOsc = this.playOsc.bind(this);
      this.draw = this.draw.bind(this);
      this.fft = new p5.constructor.FFT();
      // this.a = () => this.fft.analyze();
    }
    update = () => {
      this.pos.add(this.vel);
    };
    draw = () => {
      p5.push();
      p5.noStroke();
      p5.fill(255, 0, 0);
      p5.ellipse(this.pos.x, this.pos.y, this.size);
      p5.pop();
      if (playing) {
        () => {
          let spectrum = this.fft.analyze();
          console.log(spectrum);
        };
      } else console.log("HolaMundo");
    };
    playOsc = () => {
      this.osc.start();
      playing = true;
    };
    stopOsc = () => {
      this.osc.stop();
      playing = false;
    };
  }
};
export default sketch;
