/*eslint-disable*/
export default function sketch(p5) {
  ///Este cdigo genera una retcula de objetos cada uno relacionado a un elemento de color
  //en una lista descargada de un repositorio

  //Creo variable para descargar datos
  let data;
  //La direccin de donde obtendr mi archivo JSON, aqu est alojada en un servidor externo
  let url = 'https://raw.githubusercontent.com/dariusk/corpora/master/data/colors/crayola.json';
  //En esta variable guardar la informacin descargada
  let colores;

  //Crear una reticula de 12x10
  let columnas = 12;
  let filas = 10;
  //Variables para guardar el tamao de cada retpicula
  let ancho,alto;

  //Un arreglo para guardar los objetos de la Clase que crear
  let bolitas =[];
  //Variable de texto para guardar la etiqueta del fondo, por ahora vaca
  let nombre="";


  p5.setup = () => {
    p5.createCanvas(800, 600);
    //Descargo la informacin con el mtodo loadJSON()
    //Utilizo una funcin Callback que se ejecutar una vez descargados los datos
    data = p5.loadJSON(url,obtenerDatos);
    //Clculos de cada  espacio en la retcula
    ancho = p5.width/columnas;
    alto =  p5.height/filas;
    //Quitar contorno a los dibujos
    p5.noStroke();
  
  }

  p5.draw = () => {
    p5.background(220);
    //Formato del texto
    p5.fill(255);
    p5.textSize(50);
    p5.textAlign(p5.CENTER);
    p5.text(nombre,p5.width/2,p5.height/2);
    
    //Este pedazo de cdigo se ejecuta solamente si existe informacin en mi arreglo colores
    if(colores){
        //Para cada elemento del arreglo "bolitas" donde b es mi contador
      for(let b in bolitas){
      //Ejecuta la funcin dibujar de cada bolita
        bolitas[b].dibujar();
        //bolitas[b].mover();
      }
      
    }
  
  }
  //Funcin que se ejecuta cuando la informacin est lista
  function obtenerDatos(data){
    console.log(data);
    //Guardo en mi variable colores la informacin descargada
      colores =  data;
      //Generar un contador para cada elemento del arreglo
      let contador = 0;
      //Primer loop para las columnas 
      for(let i = 0; i<columnas;i++){
    //Segundo loop para las filas
          for(let j = 0; j<filas;j++){
              //Genero un objeto en cada repeticin cada uno tiene su posicin en x,y, diametro, color y nombre
              bolitas.push(new Bolita(i*ancho+15,j*alto+15,25,colores.colors[contador].hex, colores.colors[contador].color),
    ); 
      //Incrementar contador
      contador++;
        
      }
    }
  }

  //Aqu defino mi clase Bolita, que propiedades tendr
  class Bolita{
      //El constructor ser donde inicio las variables a usar, dentro del parntesis los argumentos
      //de entrada que podr utilizar
    constructor(_x,_y,_d,_color,_nombre){
      //Esta es la sintxis para definir cada variable dentro de mi clase
      this.x = _x;
      this.y = _y;
      this.diametro = _d;
      this.color = _color;
      this.nombre = _nombre
      
    }
    //Puedo generar nuevas funciones para cada uno de mis objetos
    //en este caso esta sera utilizada para mostrar en pantalla
    dibujar(){
      //Colorear con la informacin de color
      p5.fill(this.color);
      //Crear una elipse en cierta posicin
      p5.ellipse(this.x,this.y,this.diametro); 
      //Realizo una comparacin con dos argumentos, el primero revisa si el mouse est siendo presionado
      //el segundo la distancia entre el mouse y la posicin del objeto, si es menor al dimetro.
      //Cuando ambas condiciones se cumplen significa que la bolita est siendo presionada.
      if(p5.mouseIsPressed&&p5.dist(p5.mouseX,p5.mouseY,this.x,this.y)<this.diametro){
          //Si la bolita fue presionada, el nombre en pantalla cambia al de esta bolita
        nombre = this.nombre;
      }
      
    }
    mover(){
      this.x = this.x+p5.random(1);
      this.y = this.y+p5.random(1);
    }
    
  }


}